@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Marcellus+SC&display=swap);
body {
  margin: 0;
  font-family: 'Poppins' 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  scroll-behavior: smooth;
  /* font-family: 'Poppins' , sans-serif; */
 
}

.app {
  min-height: 100vh;
  background-color: #161623;
  color: white;
  padding-top: 90px;
  padding-bottom: 100px;
}




@media (max-width : 700px) {
  .app{
    padding-top: 70px;
    
  }
  
}

.pageTitle {
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-family: "Montserrat" , sans-serif;
  font-size: 2vw;
  padding: 4px;
  padding-bottom: 10px ;
  border-radius: 50px;
  color: white;
  font-weight: 400;
}

@media (max-width : 1000px) {
  .pageTitle{
    font-size: 4vw;
  }
  
}
.header {
    width: 100%;
    cursor: pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    background-color: #39445a;
    font-family: 'Poppins', sans-serif;
    font-size: 5vh;
    padding-bottom: 10px;
    padding-top: 10px;
    box-shadow: 0px 1px 5px black;
    color: white;
    z-index: 100;
    word-spacing: 20px;
    
} 

@media (max-width : 1000px) {
    .header{
        padding-top: 10px 0;
        font-size: 6.4vw;
    }
    
} 

/* .header_li{
    font-family: 'Poppins';
    display: flex;
    font-size: large;
    padding: 5px;

} */


.media {
    display: flex;
    flex-direction: column;
    width: 200px;
    padding: 5px;
    margin: 5px 0;
    background-color: #363c49;

    border-radius: 5px;
    position: relative;
    font-family: "Montserrat", sans-serif;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
  }
  
  .media:hover {
    background-color: rgba(255, 255, 255, 0.705);
    color: black;
    -webkit-transform: scale(1.15);
            transform: scale(1.15); 
    z-index: 100;
    opacity: 1;
    
  }
  
  @media (max-width: 550px) {
    .media {
      width: 100%;
     
      
    }
    .media:hover {
      -webkit-transform: scale(1.09);
              transform: scale(1.09); 


    }
  }
  
  .poster {
    border-radius: 3px;
  }
  
  .title {
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
    text-shadow :2px 4px 4px #4f5461 ;
  }
  
  .subTitle {
    display: flex;
    justify-content: space-between;
    padding-bottom: 3px;
    padding: 0 2px;
  }
.ContentModal__landscape {
  object-fit: contain;
  border-radius: 10px;
  display: flex ;
  
}

.ContentModal__portrait {
  display: none ;
  object-fit: contain;
  border-radius: 10px;
  width : 200px;
  width: 45%;
}

.ContentModal__portrait img{
  border: 5px solid rgb(224, 224, 224);
  border-radius: 5px;
}

.tagline {
  padding-bottom: 10px;
  align-self: center;
  font-size: 1vw;
}

.ContentModal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.ContentModal::-webkit-scrollbar {
  display: none;
}

.ContentModal__about {
  padding: 10px;
  width: 95%;
  height: 90%;
  display: flex;
  flex-direction: column;
  font-family: 'Marcellus SC', serif;
  justify-content: space-evenly;
  font-weight: 300;
  font-size: 10px;
}

.ContentModal__title {
  font-size: 5vw;
  font-family: 'Marcellus SC', serif;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ContentModal__description {
  display: flex;
  height: 30%;
  overflow-y: scroll;
  padding: 15px;
  border-radius: 5px;
  scrollbar-width: thin; /* Firefox */
  box-shadow: inset 0 0 5px #000000;
  text-align: justify;

}

.ContentModal__description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .ContentModal__landscape {
    display: none;  


  }
  .ContentModal__portrait {
    width: 10%;
    display: flex;


  }
  .ContentModal {
    flex-direction: row;
    justify-content: space-around;
    padding: 10px 0;
  }
  .ContentModal__about {
    width: 50%;
    padding: 0;
    /* height: 100%; */
    font-size: 1vh ;
  }
  .ContentModal__title {
    font-size: 3vw;
  }
  .ContentModal__description {
    font-size: 15px;
  }
}
.carouselItem {
    display: flex;
    flex-direction: column;
    object-fit: contain;
    padding: 10px;
  }
  .carouselItem__img {
    border-radius: 10px;
    margin-bottom: 5px;
    box-shadow: 0px 0px 5px black;
  }
.trending {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.search {
    display: flex;
    margin: 15px 0;
  }
