@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Marcellus+SC&display=swap');

.ContentModal__landscape {
  object-fit: contain;
  border-radius: 10px;
  display: flex ;
  
}

.ContentModal__portrait {
  display: none ;
  object-fit: contain;
  border-radius: 10px;
  width : 200px;
  width: 45%;
}

.ContentModal__portrait img{
  border: 5px solid rgb(224, 224, 224);
  border-radius: 5px;
}

.tagline {
  padding-bottom: 10px;
  align-self: center;
  font-size: 1vw;
}

.ContentModal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.ContentModal::-webkit-scrollbar {
  display: none;
}

.ContentModal__about {
  padding: 10px;
  width: 95%;
  height: 90%;
  display: flex;
  flex-direction: column;
  font-family: 'Marcellus SC', serif;
  justify-content: space-evenly;
  font-weight: 300;
  font-size: 10px;
}

.ContentModal__title {
  font-size: 5vw;
  font-family: 'Marcellus SC', serif;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ContentModal__description {
  display: flex;
  height: 30%;
  overflow-y: scroll;
  padding: 15px;
  border-radius: 5px;
  scrollbar-width: thin; /* Firefox */
  box-shadow: inset 0 0 5px #000000;
  text-align: justify;

}

.ContentModal__description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .ContentModal__landscape {
    display: none;  


  }
  .ContentModal__portrait {
    width: 10%;
    display: flex;


  }
  .ContentModal {
    flex-direction: row;
    justify-content: space-around;
    padding: 10px 0;
  }
  .ContentModal__about {
    width: 50%;
    padding: 0;
    /* height: 100%; */
    font-size: 1vh ;
  }
  .ContentModal__title {
    font-size: 3vw;
  }
  .ContentModal__description {
    font-size: 15px;
  }
}